interface OPTIONS {
  splitUnit?: boolean
  withScale?: boolean
}

const isClient = typeof window === 'object';

export const BULK_MAP = {
  en: [[1, ''], [1e3, 'K'], [1e6, 'M'], [1e9, 'B'], [1e12, 'T']],
  zh: [
    [1, ''],
    /* eslint-disable wizard/use-t-function */
    [1e4, ''],
    [1e8, ''],
    [1e12, ''],
    [1e16, ''],
    /* eslint-enable wizard/use-t-function */
  ]
}

type LocaleType = keyof typeof BULK_MAP;

/**
 * options  splitUnit & withScale
 * splitUnit  true  withScale  true
 *  [1, '' 1e4]
 */
export default function bulk(value: number, options: OPTIONS, locale?: LocaleType) {
  const lang = isClient ?
    (window.localStorage.getItem('LOCALE') && /en/.test(window.localStorage.getItem('LOCALE') as string) ? 'en' : 'zh') :
    (locale || 'zh');
  const BULK = BULK_MAP[lang as LocaleType];
  let unit = BULK[0][1];
  if (!value) {
    if (options && options.splitUnit) return [0, unit];
    return `0${unit}`;
  }
  let i = 1;
  while (BULK[i] && value >= BULK[i][0]) {
    i++;
  }
  unit = BULK[i - 1][1];
  let resultValue: number = value / +BULK[i - 1][0];
  if (parseInt(String(resultValue), 10) !== resultValue) {
    resultValue = Number(resultValue.toFixed(2));
  }
  if (options && options.splitUnit) {
    if (options.withScale) return [resultValue, unit, BULK[i - 1][0]];
    return [resultValue, unit];
  } else {
    return `${resultValue} ${unit}`;
  }
}
