import * as React from 'react';
import * as PropTypes from 'prop-types';
import { DropdownButton as BootstrapDropdownButton, MenuItem, ButtonGroup } from 'react-bootstrap';

import SubMenu from '../SubMenu';
import {
  DropdownButtonMenuItem,
  DropdownButtonProps,
  DefaultDropdownButtonProps,
} from '../../interface';
import cloneDeep from 'lodash/cloneDeep';
import omit from 'lodash/omit';
import Tooltip from '../Tooltip';
import './style.scss';

function randomId() {
  return Math.random()
    .toString(36)
    .substring(2);
}

function renderContent(
  menu: DropdownButtonMenuItem[] = [],
  setButtonOpen: Function,
  isDifferentMenu: boolean,
  open?: boolean,
) {
  if (menu instanceof Array) {
    return menu.map(m => renderMenu(m, setButtonOpen, isDifferentMenu, open));
  }
  return menu;
}

function usePrevious(value: any) {
  const ref = React.useRef();
  React.useEffect(() => {
    ref.current = value;
  });
  return ref.current;
}

function renderMenu(
  menu: DropdownButtonMenuItem,
  setButtonOpen: Function,
  isDifferentMenu: boolean,
  open?: boolean,
) {
  const item = cloneDeep(menu);
  if (!item) {
    return null;
  }
  if (typeof item === 'string') {
    return (
      <MenuItem
        key={item}
        onSelect={() => {
          setButtonOpen(!!open);
        }}
      >
        {item}
      </MenuItem>
    );
  }
  if (!item.key && isDifferentMenu) {
    item.key = randomId();
  }
  if (item.children && item.children.length) {
    return (
      <SubMenu key={item.key} title={item.title} name={item.children[0]['data-action']}>
        {renderContent(item.children, setButtonOpen, isDifferentMenu, open)}
      </SubMenu>
    );
  }
  const handleItemSelect = (eventKey: any) => {
    const { onSelect } = item;
    setButtonOpen(!!open);
    //  onSelect 
    if (onSelect) onSelect(eventKey);
  };
  const menuProps = omit(item, 'toolTip');
  return (
    <MenuItem {...menuProps} onSelect={handleItemSelect}>
      {item.toolTip ? (
        <Tooltip {...item.toolTip} placement={item.toolTip.placement || 'right'}>
          {item.toolTip.children}
        </Tooltip>
      ) : (
        item.title
      )}
    </MenuItem>
  );
}

const DropdownButton = (props: DropdownButtonProps) => {
  const getContainerClass = () => {
    const { modifer } = props;
    let className = 'dropdown-container';
    if (modifer) {
      className += ' ' + modifer;
    }
    return className;
  };
  const getOnToggle = (isOpen: boolean) => {
    const { onToggle } = props;
    //  open  MenuItem 
    if (!props.hasOwnProperty('open')) {
      setButtonOpen(isOpen);
    }
    //  onToggle 
    if (onToggle) onToggle(isOpen);
  };

  const { bsStyle, id, onSelect, bsSize, title, menu, children, componentClass, open } = props;

  const prevMenu = usePrevious(menu);
  const isDifferentMenu = prevMenu !== menu;
  const allBoolProps = ['disabled', 'dropup', 'noCaret', 'open', 'pullRight'];
  const boolProps = {};
  const [buttonOpen, setButtonOpen] = React.useState<boolean>(!!open);

  allBoolProps.forEach(prop => {
    if (props.hasOwnProperty(prop)) {
      boolProps[prop] = props[prop];
    }
  });
  const containerClassName = getContainerClass();
  return (
    <BootstrapDropdownButton
      {...boolProps}
      bsStyle={bsStyle}
      id={id}
      onSelect={onSelect}
      title={title}
      bsSize={bsSize}
      onToggle={isOpen => getOnToggle(isOpen)}
      componentClass={componentClass}
      className={containerClassName}
      open={props.hasOwnProperty('open') ? open : buttonOpen}
    >
      {menu ? renderContent(menu, setButtonOpen, isDifferentMenu, open) : children}
    </BootstrapDropdownButton>
  );
};

DropdownButton.propTypes = {
  /**
   *  stringdefaultprimarysuccessinfowarningdangerdefault
   **/
  bsStyle: PropTypes.string,
  /**
   *  nodemenu
   **/
  children: PropTypes.node,
  /**
   *  elementType
   **/
  componentClass: PropTypes.any,
  /**
   *  stringtable-toolbar, list-toolbar, detail-toolbar
   **/
  modifer: PropTypes.string,
  /**
   *  bool
   **/
  disabled: PropTypes.bool,
  /**
   *  bool
   **/
  dropup: PropTypes.bool,
  /**
   * html id string
   **/
  id: PropTypes.string.isRequired,
  /**
   *  array<string|object>, itemstringitemMenuItemkeychildren
   * objecttitleMenuItemchildrenMenuItemkeytitlekey
   * childrenchildrenSubItem
   **/
  menu: PropTypes.array,
  /**
   *  bool
   **/
  noCaret: PropTypes.bool,
  /**
   *  function (eventKey: any, event: Object) => any
   **/
  onSelect: PropTypes.func,
  /**
   *  bool
   * openMenuItem
   * openopen
   **/
  open: PropTypes.bool,
  /**
   *  function function(Boolean isOpen, Object event, { String source }) {}
   **/
  onToggle: PropTypes.func,
  /**
   *  bool
   **/
  pullRight: PropTypes.bool,
  /**
   *  stringlargedefaultsmallxsmalldefault
   **/
  bsSize: PropTypes.oneOf(['xs', 'sm', 'medium', 'lg']),
  /**
   *  string|element
   **/
  title: PropTypes.oneOfType([PropTypes.element, PropTypes.string]),
};

const defaultProps: DefaultDropdownButtonProps = { componentClass: ButtonGroup };

DropdownButton.defaultProps = defaultProps;

export default DropdownButton;
